.portfolio {
   background-color: #cbf11e;
   position: relative;
   // .portfolio__container

   &__container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 50px 0 160px;
   }

   // .portfolio__title

   &__title {
      margin: 0 0 100px 0;
   }

   // .portfolio__content

   &__content {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 30px;
      width: 100%;
      margin: 0 0 100px 0;
      @media (max-width: 768px) {
         grid-template-columns: repeat(1, 1fr);
      }
   }

   &__item:nth-child(n + 7) {
      display: none;
   }
   &__item.is-visible {
      display: block;
   }
   &__block {
      position: relative;
      width: 100%;
      height: 400px;
      box-shadow: 10px 5px 5px #097d07;
      transition: all 0.5s ease-in-out;
      &:hover {
         box-shadow: 20px 15px 15px #097d07;
      }
      @media (max-width: 1440px) {
         height: 300px;
      }
      @media (max-width: 1024px) {
         height: 250px;
      }
      @media (max-width: 768px) {
         height: 350px;
      }
      @media (max-width: 576px) {
         height: 250px;
      }
      @media (max-width: 480px) {
         height: 200px;
      }
   }
   &__image {
      position: absolute;
      inset: 0;
      img {
         position: absolute;
         inset: 0;
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
   }
   &__box {
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 20px;
      width: 100%;
      height: 100%;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;

      &::before {
         content: "";
         position: absolute;
         inset: 0;
         background-color: rgba(0, 0, 0, 0.8);
      }
      &:hover {
         opacity: 1;
      }
   }
   &__row {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      flex-grow: 1;
   }
   &__title-card {
      position: relative;
      font-size: 28px;
      font-weight: 700;
      color: #fff;
      z-index: 10;
      text-transform: uppercase;
      margin: 30px 0 50px 0;
      @media (max-width: 1024px) {
         font-size: 24px;
         margin: 0 0 20px 0;
      }
      @media (max-width: 480px) {
         font-size: 18px;
         margin: 0 0 10px 0;
      }
   }
   &__descr-card {
      font-size: 20px;
      color: #fff;
      z-index: 10;
      margin: 0px 0 40px 0;
      @media (max-width: 1024px) {
         font-size: 18px;
         margin: 0 0 20px 0;
      }
      @media (max-width: 480px) {
         font-size: 16px;
         margin: 0 0 10px 0;
      }
   }
   &__tech-card {
      color: #fff;
      z-index: 10;
      margin: 0px 0 20px 0;
      @media (max-width: 1024px) {
         font-size: 14px;
         margin: 0 0 20px 0;
      }
      @media (max-width: 480px) {
         font-size: 14px;
         margin: 0 0 20px 0;
      }
   }
   &__button-card {
      margin: 0 0 25px 0;
      padding: 20px;
      color: #fff;
      z-index: 10;
   }
}
