.about {
   &__container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 150px 0 0 0;
      @media (max-width: 768px) {
         padding: 100px 0 0 0;
      }
   }

   &__title {
      margin: 0 0 100px 0;
   }
   &__subtitle {
      font-weight: 600;
      font-size: 32px;
      margin: 0 0 40px 0;
      color: #faaf52;
   }

   &__list {
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
      height: 100%;
      width: 100%;
      margin-bottom: 70px;
      @media (max-width: 768px) {
         margin-bottom: 50px;
      }
   }

   &__cart {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      padding: 20px;
      margin: 0 10px 40px 10px;
      box-shadow: 5px 5px 10px #056748;
      transition: box-shadow 0.3s ease;
      &:hover {
         box-shadow: 10px 10px 15px #056748;
      }
   }

   &__image {
      margin: 0 0 30px 0;
   }
   &__cart-box {
      flex-grow: 1;
      display: flex;
      flex-direction: column;
   }
   &__cart-title {
      font-weight: 600;
      font-size: 24px;
      margin: 0 0 20px 0;
      flex-grow: 1;
      @media (max-width: 768px) {
         max-width: 200px;
         font-size: 20px;
      }
   }

   &__descr {
      max-width: 290px;
      margin: 0 0 10px;

      @media (max-width: 1440px) {
         max-width: 250px;
      }
      @media (max-width: 768px) {
         max-width: 200px;
         font-size: 16px;
      }
   }
   &__success {
      display: flex;
      justify-content: space-around;
      width: 100%;
      margin-bottom: 50px;
      flex-wrap: wrap;
   }
   &__success-item {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      width: 30%;
      height: 100px;
      background-color: var(--green-color);
      padding: 10px;
      margin: 0 20px 20px;
      font-size: 28px;
      box-shadow: 10px 5px 15px #056748;
      transition: box-shadow 0.3s ease;
      span {
         font-size: 48px;
         font-weight: 700;
         margin-right: 30px;
      }
      &:hover {
         box-shadow: 15px 10px 15px #056748;
      }
      @media (max-width: 1200px) {
         width: 40%;
      }
      @media (max-width: 840px) {
         font-size: 24px;
         span {
            font-size: 32px;
         }
      }
      @media (max-width: 768px) {
         font-size: 20px;
         height: 70px;
         span {
            font-size: 24px;
            margin-right: 20px;
         }
      }
      @media (max-width: 576px) {
         width: 280px;
      }
   }
}
